nav{
  display:flex;
  justify-content: space-evenly;
  border-top: 1px solid gray;
  border-bottom: 6px double black;
  padding: 0.2% 2%;
}
nav a{
  text-decoration: none;
  color: gray;
}
nav a:hover{
  color: black;
}
.divtop{
  color: white;
  background-color: rgb(19,19,19);
  display: flex;
  justify-content: space-between;
  padding:1% 10% ;
}
.divtop p{
  display: flex;flex-direction: column;
  align-items: center;
}
.divtop h1{
  padding:0px 1% ;
  text-align: center;
  border-bottom: 4px double white;
  border-top: 4px double white;
}
.card{
  border: 10px double white;
}
.card-body{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.card-body a{
  width: 150px;
}
.pagenews, .btnn{
  width: 80%;
  margin: auto;
}
.pagenews img{
  height: 100%;
  border-radius: 5px;
}
.whlens{
  margin-top: 0.2%;
  display: flex;
  background-color: rgb(19,19,19);
  margin: auto;
  width: 80%; justify-content:space-between;
  align-items: center;
  color: white;

}

.newspage{
  background-color: rgb(19,19,19);
}.btnn{
  display: flex;
  
  justify-content: space-between;
}

@media screen and (max-width:800px)
 {
  .divtop{
    display: flex;
    flex-direction: column;
 } }
